import global from '../../mixins/global.js'
export default {
  mixins: [global],
  data() {
    return {
      lists:[],
      default_id:'',
      addr_item:''
    };
  },
  onLoad:function(){
  },
  onShow(){
    this.getLists()
  },
  methods: {
    editAddr(id){
      this.toQueryLink('/pages/checkout/addCheckoutAdd',{addr_id:id})
    },
    deleteAddr(id){
      const _this = this
      uni.showModal({
        content:'确定删除该数据？',
        success: function (info) {
          if (info.confirm) {
            _this.$http({
              aurl:'deleteAddress',
              params:{
                addr_id:id
              },
              success:res => {
                if(res.data.code === 0){
                  _this.lists.forEach((item,index)=>{
                    if(item.id===id){
                      _this.lists.splice(index,1)
                    }
                  })
                }
              },
              fail:error => {
                console.log(error)
              }
            })
          }
        }
      })
    },
    getAddr(){
      const _this = this
      uni.getSetting({
        success(res) {
          if (res.authSetting['scope.address']) {
            uni.chooseAddress({
              success(res) {
                const item ={
                  addr_id:0,
                  name:res.userName,
                  tel:res.telNumber,
                  province:res.provinceName,
                  city:res.cityName,
                  county:res.countyName,
                  address:res.detailInfo,
                  area_code:res.nationalCode,
                  is_default:0,
                  postal_code:res.postalCode
                }
                _this.$http({
                  aurl:'saveAddress',
                  data:item,
                  success:res => {
                    if(res.data.code===0){
                      _this.getLists()
                    }
                  },
                  fail:error => {
                    console.log(error)
                  }
                })
              }
            })
          } else {
            if (res.authSetting['scope.address'] == false) {
              uni.openSetting({
                success(res) {
                  console.log(res.authSetting)
                }
              })
            } else {
              uni.chooseAddress({
                success(res) {
                  const item ={
                    addr_id:0,
                    name:res.userName,
                    tel:res.telNumber,
                    province:res.provinceName,
                    city:res.cityName,
                    county:res.countyName,
                    address:res.detailInfo,
                    area_code:res.nationalCode,
                    is_default:0,
                    postal_code:res.postalCode
                  }
                  _this.$http({
                    aurl:'saveAddress',
                    data:item,
                    success:res => {
                      if(res.data.code===0){
                        _this.getLists()
                      }
                    },
                    fail:error => {
                      console.log(error)
                    }
                  })
                }
              })
            }
          }
        }
      })
    },

    getLists(){
      this.$http({
        aurl:'userAddress',
        success:res => {
          this.lists = res.data.result.lists
          this.default_id = res.data.result.default_id
        },
        fail:error => {
          console.log(error)
        }
      })
    },
  },
  mounted() {
  },
};
